.Scrollable {
  --scrollable-overlay-height: var(--vertical-spacing);
  --scrollable-overlay-color: white;

  position: relative;
  overflow: hidden;
  height: 100%;
}

.Scrollable::before,
.Scrollable::after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  height: var(--scrollable-overlay-height);
  width: 100%;
}

.Scrollable::before {
  top: 0;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(5%, var(--scrollable-overlay-color)),
    to(transparent)
  );
  background: -webkit-linear-gradient(
    top,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
  background: -o-linear-gradient(
    top,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
  background: linear-gradient(
    to bottom,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
}
.Scrollable-Wrapper {
  position: relative;
  z-index: 0;
  overflow-y: auto;
  padding: var(--scrollable-overlay-height) 0;
  height: 100%;
}
.Scrollable::after {
  bottom: 0;
  background: -webkit-gradient(
    linear,
    left bottom, left top,
    color-stop(5%, var(--scrollable-overlay-color)),
    to(transparent)
  );
  background: -webkit-linear-gradient(
    bottom,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
  background: -o-linear-gradient(
    bottom,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
  background: linear-gradient(
    to top,
    var(--scrollable-overlay-color) 5%,
    transparent
  );
}



