.WelcomePage {
  font-size: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: var(--vertical-spacing);
}

@media (min-height: 20rem) {
  .WelcomePage {
    height: 100%;
  }
}

.WelcomePage-Message,
.WelcomePage-Actions {
  -ms-flex: none;
      flex: none;
}

.WelcomePage-Message {
  font-size: var(--font-size-large);
  color: var(--text-color-secondary);
  text-align: center;
}

.WelcomePage-Message :first-child {
  font-size: var(--font-size-extra-large);
}

.WelcomePage-Actions {
  margin-top: var(--vertical-spacing);
}



